body {
    background-image: url("../../assets//brown-paper-clips-1097930.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;
}

.Content {
    position: relative;
    margin: 0 auto;
}

.Paragraph {
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.7);
    color: #f1f1f1;
    width: 100%;
    padding: 20px;
    margin-bottom: 100px;
}

.Icon {
    display: flex;
    justify-content: center;
    color: black;
    font-size: 160px;
    margin-top: 20px;
    margin-bottom: 20px;
}

@media only screen and (max-width: 600px) {
    .Paragraph {
        background: rgba(0, 0, 0, 0);
        color: black;
        padding: 10px;
    }
    .Icon {
        font-size: 100px;
    }
}